function setImagePosition(e) {
  var x = -e.offsetX + 'px';
  var y = -e.offsetY + 'px';
  this.style.transform = "translate(".concat(x, ", ").concat(y, ") scale(2)");
}

function setImageImagePosition(e) {
  this.style.transform = 'translate(0, 0) scale(1)';
}

